
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "CollapsibleTile",
  props: {
    title: {
      type: String,
      required: true,
    },
    initiallyCollapsed: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const isCollapsed = ref(props.initiallyCollapsed);

    const toggle = () => {
      isCollapsed.value = !isCollapsed.value;
    };

    return {
      isCollapsed,
      toggle,
    };
  },
});
